.shopTitle {
    margin-top: 100px;
    text-align: center;
    font-size: 40px;
    align-items:center;
  }
  .products {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  }
  
  .product {
    border-radius: 15px;
    width: 300px;
    height: 350px;
    margin: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .product img {
    width: 400px;
  }

  
  .product .description {
    text-align: center;
  }
  
  .product:hover {
    transition: 0.3s ease-in;
    cursor: pointer;
    opacity:70%;
  }
  
  .addToCartBttn {
    background-color: transparent;
    border: 2px solid rgb(19, 19, 19);
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
  
  .addToCartBttn:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
  }

  @media (max-width:1000px){
    .products {
      width: 100%;
      height: auto;
      display: grid;
      grid-template-columns: 1fr 1fr;
      place-items: center;
    }
    
    .product img {
      width: 300px;
    }
  }
  @media (max-width:600px){
    .products {
      width: 100%;
      height: auto;
      display: grid;
      grid-template-columns: 1fr;
      place-items: center;
    }
    
    .product img {
      width: 220px;
    }
    .product {
      border-radius: 15px;
      width: 50px;
      height: 250px;
      /* margin: 100px; */
      display: flex;
      flex-direction: column;
      /* justify-content: center; */
      /* align-items: center; */
    }
    .shopTitle {
      margin-top: 100px;
      text-align: center;
      font-size: 20px;
      align-items:center;
    }
  }